



















import Vue from 'vue'
import SmallProjectPattern from '@/components/SmallProjectPattern.vue'
import BigProjectPattern from '@/components/BigProjectPattern.vue'

import {ProjectsData} from '@/assets/ProjectsData.ts'

export default Vue.extend({
    name:"VideoProject",
    components:{
        SmallProjectPattern,
        BigProjectPattern
    },
    data: function(){
        return{
            ProjectData: null
        }
    },
    created: function(){
        this.ProjectData = this.searchById(this.$route.path.substr(10), ProjectsData);
    },
    methods:{
      searchById(nameKey, myArray){
      for (let i=0; i < myArray.length; i++) {
        if (myArray[i].id === nameKey) {
            return myArray[i];
        }
      }
    }
    }
    
})
